* {
  padding: 0;
  margin: 0;
}

html {
  height: 100%;
}

body {
  min-width: 380px;
  height: 100%;
  line-height: normal;
}

a {
  color: inherit;
  text-decoration: none;
}

a:visited {
  color: inherit;
  text-decoration: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 1em;
}

ul,
ol {
  list-style: none;
}

em {
  font-style: normal;
}

table {
  border-collapse: separate;
  border-spacing: 0;
  table-layout: fixed;
}

table caption {
  overflow: hidden;
  width: 0;
  height: 0;
  font-size: 0;
  line-height: 0;
  text-indent: -9999px;
}

legend {
  display: none;
  width: 0;
  height: 0;
  overflow: hidden;
  font-size: 0;
  line-height: 0;
  text-indent: -9999px;
}

button {
  border: none;
  background: none;
  cursor: pointer;
  outline: none;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

button:focus > *,
button:active > * {
  position: relative;
  top: 0;
  left: 0;
}

button:focus:before, button:active:before {
  position: relative;
  top: 0;
  left: 0;
}

.list-card {
  height: auto;
  background-color: white;
  padding: 5px;
  border-radius: 5px;
  -webkit-box-shadow: 1px 1px 1px rgba(0, 0, 0, 0.2);
          box-shadow: 1px 1px 1px rgba(0, 0, 0, 0.2);
  cursor: move;
  cursor: -webkit-grab;
  cursor: grab;
}

.list-card > .list-image > .img {
  height: 150px;
  background-size: cover;
  background-position: 50%;
  border-radius: 5px;
}

.list-card > .list-text {
  padding: 5px 0;
}

.list-card > .list-badges {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}

.list-card > .list-badges > .badge {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 18px;
  height: 18px;
}

.list-card > .list-badges > .badge > span {
  display: inline-block;
  width: 100%;
  height: 100%;
}

.cardtitle {
  float: left;
  width: 100%;
}

.cards .is-moving {
  background-color: white;
  color: black;
}

.noselect {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.gu-mirror {
  cursor: move;
  cursor: -webkit-grabbing;
  cursor: grabbing;
  position: fixed !important;
  margin: 0 !important;
  z-index: 9999 !important;
  opacity: 1;
  -webkit-transform: rotate(5deg);
  transform: rotate(5deg);
}

.gu-hide {
  display: none !important;
}

.gu-unselectable {
  -webkit-user-select: none !important;
  -moz-user-select: none !important;
  -ms-user-select: none !important;
  user-select: none !important;
}

.gu-transit {
  opacity: 0.4;
  -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=20)";
  filter: alpha(opacity=20);
}
